@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/museoSansCyrl-300.woff2") format("woff2"), url("../fonts/museoSansCyrl-300.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/museoSansCyrl-500.woff2") format("woff2"), url("../fonts/museoSansCyrl-500.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/museoSansCyrl-700.woff2") format("woff2"), url("../fonts/museoSansCyrl-700.woff") format("woff");
  font-display: swap; }

body {
  padding: 0;
  margin: 0;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: black;
  font-style: normal;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  display: block;
  padding: 10px 8px;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 17px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  background-color: transparent;
  border: 0.5px solid #E0E0E0;
  border-radius: 5px;
  box-sizing: border-box;
  outline: none;
  cursor: pointer; }
  .button:hover {
    color: #00AED6;
    background-color: #ffffff;
    border-color: #ffffff; }
  .button:active {
    color: #00AED6;
    background-color: #ffffff;
    border-color: #ffffff; }
  .button:focus {
    color: #00AED6;
    background-color: #ffffff;
    border-color: #ffffff; }

.button span {
  display: inline-block;
  margin-left: 4px;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.05em;
  color: #E0E0E0;
  text-transform: uppercase; }

.button--shops:hover {
  color: #65AA0F; }

.button--shops:active {
  color: #65AA0F; }

.button--shops:focus {
  color: #65AA0F; }

.button--shops:hover span {
  color: #65AA0F; }

.button--shops:active span {
  color: #65AA0F; }

.button--shops:focus span {
  color: #65AA0F; }

.page-main__box {
  max-width: 1024px;
  margin: 0 auto;
  background-color: #ffffff;
  border-radius: 5px; }

.events {
  background-color: #00AED6; }

.events__box {
  width: 1024px;
  margin: 0 auto; }

.events h2 {
  margin: 0;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 48px;
  line-height: 44px;
  letter-spacing: -0.01em;
  color: #ffffff; }

.events__list {
  display: flex;
  margin-bottom: 24px; }

.events__button {
  margin-left: 8px; }

.events__button:first-child {
  margin-left: 0; }

.events__items {
  display: flex; }

.events__item:nth-child(2n) {
  margin-left: 24px;
  margin-right: 25px; }

.card {
  display: flex;
  flex-direction: column;
  width: 325px;
  min-height: 525px;
  padding-bottom: 24px;
  background-color: #ffffff;
  border-radius: 5px;
  box-sizing: border-box;
  overflow: hidden; }

.card img {
  display: block;
  width: 325px;
  height: 325px;
  margin-bottom: 24px;
  background-color: pink; }

.card h3 {
  width: 275px;
  margin: 0;
  margin-left: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 23px;
  letter-spacing: -0.01em;
  color: #222222; }

.card__box {
  display: flex;
  justify-content: space-between;
  width: 275px;
  margin-top: auto;
  margin-left: 24px; }

.card__box p {
  margin: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #222222; }

.card__box p span {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 8px; }

.card__box a {
  display: block;
  width: 19px;
  height: 8px; }

.card__clock {
  fill: #7D7D7D; }

.card__arrow {
  fill: #00AED6; }

.subscription {
  width: 325px;
  min-height: 525px;
  padding: 24px;
  background-color: #ffffff;
  border-radius: 5px;
  box-sizing: border-box; }

.subscription h3 {
  width: 275px;
  margin: 0;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 24px;
  line-height: 23px;
  letter-spacing: -0.01em;
  color: #222222; }

.subscription__latter {
  margin-bottom: 69px; }

.subscription p {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #7D7D7D; }

.subscription__form input {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 16px;
  border: 1px solid #E0E0E0;
  border-radius: 5px 5px 0 0;
  box-sizing: border-box; }
  .subscription__form input::placeholder {
    color: #7D7D7D; }

.subscription__button {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  color: #00AED6;
  border: 1px solid #E0E0E0;
  border-top: none;
  border-radius: 0 0 5px 5px;
  background-color: transparent;
  box-sizing: border-box;
  cursor: pointer; }

.subscription__social {
  margin-bottom: 24px; }

.subscription__list,
.subscription__items {
  display: flex;
  border: 1px solid #E0E0E0;
  border-radius: 5px; }

.subscription__icon {
  width: 56px;
  height: 48px;
  border-right: 1px solid #E0E0E0; }

.subscription__fb {
  margin-top: 12px;
  margin-left: 23px; }

.subscription__vk {
  margin-top: 15px;
  margin-left: 13px; }

.subscription__od {
  margin-top: 11px;
  margin-left: 20px; }

.subscription__youtube {
  margin-top: 14px;
  margin-left: 15px; }

.subscription__icon--youtube {
  border-right: none; }

.subscription__app {
  height: 48px; }

.subscription__app--apple {
  width: 136px;
  background-image: url("../img/app-store.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 92px 23px;
  border-right: 1px solid #E0E0E0; }

.subscription__app--android {
  width: 142px;
  background-image: url("../img/google-play.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100px 24px; }

.shops {
  background-color: #1EC1A4; }

.shops__box {
  width: 1024px;
  margin: 0 auto; }

.shops__box h2 {
  margin: 0;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 48px;
  line-height: 44px;
  letter-spacing: -0.01em;
  color: #ffffff; }

.shops__list {
  display: flex;
  flex-wrap: wrap; }

.shops__button {
  margin-right: 8px;
  margin-bottom: 8px; }
